// GNOME / Adwaita color palette
:root {
  --blue-1: #99c1f1; --blue-2: #62a0ea; --blue-3: #3584e4; --blue-4: #1c71d8; --blue-5: #1a5fb4;
  --dark-1: #77767b; --dark-2: #5e5c64; --dark-3: #3d3846; --dark-4: #241f31; --dark-5: #1a1a1a;
  --light-1: #ffffff; --light-2: #f6f5f4; --light-3: #deddda; --light-4: #c0bfbc;
  --radius: 12px;
  --shadow: 0 2px 8px 0 rgba(0,0,0,.14);
}

[data-theme="dark"] {
  --bg: var(--dark-4); --bg-alt: var(--dark-3);
  --fg: #fff; --fg-alt: #deddda;
}

[data-theme="light"] {
  --bg: var(--light-2); --bg-alt: var(--light-1);
  --fg: var(--dark-4); --fg-alt: var(--dark-2);
}

* { box-sizing: border-box; }
body {
  margin: 0;
  font-family: "Cantarell", "Inter", system-ui, sans-serif;
  background: var(--bg);
  color: var(--fg);
  line-height: 1.6;
  transition: background .3s ease, color .3s ease;
}
a { color: var(--blue-3); text-decoration: none; }
img { max-width: 100%; display: block; }

/* Header */
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
  background: var(--bg-alt);
  box-shadow: var(--shadow);
  position: sticky;
  top: 0;
  z-index: 10;
}
header h1 { margin: 0; font-size: 1.5rem; }
.theme-toggle {
  cursor: pointer;
  font-size: 1.25rem;
  background: none;
  border: none;
  color: var(--fg);
}

/* Landing grid */
.grid {
  display: grid;
  gap: 2rem;
  padding: 3rem 2rem;
  max-width: 1200px;
  margin: auto;
  grid-template-columns: repeat(auto-fill,minmax(260px,1fr));
}
.card {
  background: var(--bg-alt);
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  overflow: hidden;
  transition: transform .2s;
}
.card:hover { transform: translateY(-4px); }
.card img { width: 100%; height: 160px; object-fit: cover; }
.card-body { padding: 1.25rem; }
.card-body h3 { margin: 0 0 .5rem; font-size: 1.25rem; }
.card-body p  { margin: 0 0 1rem; color: var(--fg-alt); font-size: .9rem; }

/* App page hero */
.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 4rem 2rem;
}
.hero img { width: 128px; height: 128px; border-radius: var(--radius); }
.hero h1 { margin: 1rem 0 .5rem; font-size: 2.5rem; }
.hero p  { font-size: 1.25rem; color: var(--fg-alt); }

.buttons { display: flex; gap: 1rem; flex-wrap: wrap; justify-content: center; margin: 2rem 0; }
.btn {
  display: inline-flex;
  align-items: center;
  gap: .5rem;
  padding: .75rem 1.5rem;
  border-radius: var(--radius);
  font-weight: 600;
  transition: .2s;
}
.btn.primary {
  background: var(--blue-3);
  color: #fff;
}
.btn.primary:hover { background: var(--blue-4); }
.btn.secondary {
  background: var(--bg-alt);
  color: var(--fg);
  border: 1px solid var(--light-4);
}
.btn.secondary:hover { background: var(--light-3); }

/* Screenshots */
.screenshots {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit,minmax(300px,1fr));
  padding: 0 2rem 3rem;
}
.screenshots img {
  border-radius: var(--radius);
  box-shadow: var(--shadow);
}
